.liked {
    cursor: pointer;
    position: absolute;
    right: 20px;
    bottom: 20px;
    width: 20px;
    height: 20px;
    background: no-repeat center/100% url('../../assets/img/like_icon.svg');
}

.unliked {
    cursor: pointer;
    position: absolute;
    right: 20px;
    bottom: 20px;
    width: 20px;
    height: 20px;
    background: no-repeat center/100% url('../../assets/img/unlike_icon.svg');
}